/* Container for the animated section */
.animatedSection {
    position: relative;
    width: 100%;
    height: 300px; /* Adjust height as needed */
    overflow: hidden;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #f4f4f4;
  }
  
  /* Shared styles for content */
  .content {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: opacity 1s ease-in-out, transform 1s ease-in-out;
  }
  
  /* Active content (visible) */
  .active {
    opacity: 1;
    transform: translateY(0);
    z-index: 1;
  }
  
  /* Hidden content (invisible and moved slightly up or down) */
  .hidden {
    opacity: 0;
    transform: translateY(20px); /* Slide it slightly up/down during the transition */
    z-index: 0;
  }
  
  h1 {
    font-size: 36px;
    margin-bottom: 10px;
    color: #333;
  }
  
  p {
    font-size: 18px;
    color: #666;
    max-width: 600px;
  }
  