/* Container for the entire skills section */
.skillsContainer {
  text-align: center;
  padding: 40px 20px;
}

/* Grid layout for the skill cards */
.skillsGrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr)); /* Flexible layout */
  gap: 20px;
  margin-top: 20px;
}

/* Individual skill card styling */
.skillCard {
  background: #ffffff;
  border-radius: 10px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  padding: 20px;
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  border-bottom: 4px solid transparent;
}

.skillCard:hover {
  transform: translateY(-5px);
  box-shadow: 0px 8px 10px rgba(0, 0, 0, 0.15);
}

/* Hexagonal icon container */
.iconHexagon {
  position: relative;
  width: 70px;
  height: 80px;
  margin: 0 auto 15px;
  clip-path: polygon(
    50% 0%, 
    100% 25%, 
    100% 75%, 
    50% 100%, 
    0% 75%, 
    0% 25%
  );
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f5f5f5;
}

.iconHexagon:before {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: inherit;
  z-index: -1;
}

/* Icon styling */
.iconContainer {
  font-size: 36px; /* Size of the icon */
  color: #fff;
}

/* Title styling for each skill */
.skillTitle {
  font-weight: bold;
  font-size: 18px;
  margin-bottom: 10px;
  color: #333;
}

/* Chip container styling */
.skillItems {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
  margin-top: 0px;
  justify-content: center;
}

/* Individual chip styling */
.skillChip {
  background-color: #ffffff;
  color: #333;
  padding: 3px 5px;
  font-size: 12px;
  border-radius: 8px;
  border: 1px solid #ddd;
  transition: background-color 0.3s ease;
}

.skillChip:hover {
  background-color: #e0e0e0;
  cursor: pointer;
}

/* Media Queries for Responsiveness */

/* For phones and small devices */
@media (max-width: 768px) {
  .skillsContainer {
    padding: 20px 10px; /* Reduce padding on smaller screens */
  }

  .skillsGrid {
    grid-template-columns: 1fr; /* Single-column layout */
  }

  .skillCard {
    padding: 15px; /* Less padding to fit better on smaller screens */
  }

  .iconHexagon {
    width: 60px;
    height: 70px; /* Slightly smaller hexagon */
  }

  .iconContainer {
    font-size: 28px; /* Smaller icon size */
  }

  .skillTitle {
    font-size: 16px; /* Slightly smaller title text */
  }

  .skillChip {
    font-size: 10px; /* Smaller font for chips */
    padding: 2px 4px;
  }
}
