/* Sidebar container */
.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  width: 250px; /* Sidebar width */
  height: 100%; /* Full height */
  background: #f4f4f4;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1);
  padding: 20px;
  display: flex;
  flex-direction: column; /* Stack content vertically */
  align-items: center; /* Center-align content horizontally */
  transform: translateX(-100%); /* Hidden by default for mobile */
  transition: transform 0.3s ease-in-out;
  z-index: 1000;
}

.sidebar.open {
  transform: translateX(0); /* Show sidebar */
}

/* Hamburger Menu */
.hamburger {
  position: fixed;
  top: 15px;
  left: 15px;
  width: 50px; /* Adjust circle size */
  height: 50px;
  background: #fff; /* White background */
  border-radius: 50%; /* Make it circular */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.15); /* Add a subtle shadow */
  display: flex;
  justify-content: center; /* Center the icon horizontally */
  align-items: center; /* Center the icon vertically */
  font-size: 24px;
  color: #333; /* Icon color */
  cursor: pointer;
  z-index: 1100; /* Ensure it's above the sidebar */
  transition: background 0.3s ease, color 0.3s ease;
}

.hamburger:hover {
  background: #f4f4f4; /* Slightly darker hover background */
  color: #007bff; /* Change icon color on hover */
}

.hamburger:focus {
  outline: none; /* Remove focus outline */
  box-shadow: 0 0 0 3px rgba(0, 123, 255, 0.25); /* Add focus ring for accessibility */
}


/* Profile Image */
.profileImage {
  width: 120px;
  height: 120px;
  border-radius: 50%; /* Make it circular */
  object-fit: cover; /* Ensure proper image scaling */
  margin-bottom: 20px;
}

/* Name (Centered Text) */
h1 {
  margin: 0;
  font-size: 22px; /* Adjust font size */
  text-align: center; /* Center-align text */
}

/* Job Title */
.jobTitle {
  margin: 10px 0;
  font-size: 16px;
  color: #555;
  text-align: center;
}

/* Contact Icons */
.contactIcons {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
}

.iconLink {
  font-size: 20px;
  color: #333;
  text-decoration: none;
  transition: color 0.3s ease;
}

.iconLink:hover {
  color: #007bff;
}

/* Navigation Links */
.navLinks {
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%;
}

.navLinks li {
  font-size: 16px;
  margin: 10px 0;
  cursor: pointer;
  text-align: center;
  transition: color 0.3s ease;
}

.navLinks li:hover {
  color: #007bff;
}

/* Footer */
.footer {
  margin-top: 20px; /* Add space between the footer and the last nav link */
  font-size: 14px;
  color: #aaa;
  text-align: center; /* Center-align copyright text */
}

/* Main Content Wrapper */
.mainContent {
  transition: margin-left 0.3s ease-in-out;
  padding: 20px;
}

.mainContent.sidebarOpen {
  margin-left: 250px; /* Reserve space for sidebar */
}

.mainContent.sidebarHidden {
  margin-left: 0; /* Center content when sidebar is hidden */
}

/* Center Wrapper for Content */
.contentWrapper {
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
}

/* Media Queries */
@media (max-width: 768px) {
  .sidebar {
    width: 200px; /* Smaller sidebar on phones */
    transform: translateX(-100%); /* Hidden by default */
  }

  .sidebar.open {
    transform: translateX(0); /* Show sidebar */
  }

  .mainContent {
    padding: 10px;
  }

  .contentWrapper {
    max-width: 90%; /* Reduce max-width on smaller screens */
  }
}
