/* Container for the entire section */
.experienceContainer {
    padding: 40px 20px;
    text-align: center;
  }
  
  h2 {
    font-size: 32px;
    margin-bottom: 20px;
    color: #333;
  }
  
  /* Grid Layout for Experience Cards */
  .experienceGrid {
    display: flex;
    flex-direction: column; /* Stack the cards vertically */
    gap: 40px; /* Add space between each job */
    margin-top: 20px;
  }
  
  /* Experience Card Styling */
  .experienceCard {
    background: #ffffff;
    border-radius: 10px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    padding: 20px;
    text-align: left;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    border-left: 6px solid transparent;
    display: flex;
    flex-direction: column;
    position: relative; /* To position both the duration and location */
  }
  
  .experienceCard:hover {
    transform: translateY(-5px);
    box-shadow: 0px 8px 10px rgba(0, 0, 0, 0.15);
  }
  
  /* Job Title Styling */
  .jobTitle {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 5px;
    color: #333;
  }
  
  /* Company Name Styling (Italic) */
  .company {
    font-size: 16px;
    font-style: italic;
    color: #666;
    margin-bottom: 10px;
  }
  
  /* Duration Styling (Aligned Right) */
  .duration {
    position: absolute;
    top: 20px; /* Align with top padding */
    right: 20px;
    font-size: 14px;
    color: #999;
  }
  
  /* Location Styling (Right-Aligned Below Date) */
  .location {
    position: absolute;
    top: 45px; /* Position it below the date */
    right: 20px;
    font-size: 14px;
    font-style: italic;
    color: #666;
  }
  
  /* Job Details Styling */
  .jobDetails {
    list-style-type: disc;
    margin-left: 20px;
  }
  
  .jobDetails li {
    font-size: 14px;
    margin-bottom: 8px;
    color: #555;
  }
  
  /* Achievements Title Styling */
  .achievementsTitle {
    font-size: 16px;
    margin-top: 15px;
    margin-bottom: 8px;
    font-weight: bold;
    color: #333;
  }
  