/* Main Content Wrapper */
.mainContent {
  padding: 20px;      /* Keep padding inside the content area */
  margin-left: 300px; /* Default margin when sidebar is open */
  transition: margin-left 0.3s ease-in-out, max-width 0.3s ease-in-out; /* Smooth transition */
}

/* When sidebar is open */
.sidebarOpen {
  margin-left: 300px; /* Match the width of the sidebar */
  max-width: calc(100% - 300px); /* Content takes up remaining space */
}

/* When sidebar is hidden */
.sidebarHidden {
  margin-left: 0; /* Center the content */
  max-width: 100%; /* Content takes full width */
}

/* Section styles */
section {
  margin-bottom: 80px; /* Space between sections */
}

section h2 {
  margin-bottom: 20px;
}

/* Lists */
ul {
  margin: 10px 0 20px 20px;
  padding: 0;
  list-style-type: disc;
}

li {
  margin-bottom: 10px;
  font-size: 16px;
  color: #333;
}
